import apiBlog from '../../api/blogs'

// initial state
const state = {
  blogs: []
}

// getters
const getters = {}

// actions
const actions = {
  async getAllBlogs ({ commit }) {
    this._vm.$root.api_load = 'aaa';
    console.log( this._vm );
    apiBlog.getBlogs(datas => {
      commit('setStateBlogs', datas)
    })
  }
}

// mutations
const mutations = {
  setStateBlogs (state, datas) {
    state.blogs = datas
  },
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}


