  description: "Command to see what the current state of our project",
  content: 'Next up, let\'s type the git status command to see what the current state of our project is:\n        <pre><code>git status</code></pre>\n        <p class="text-right"><a href="https://git-scm.com/docs/git-status">source</a></p>'
