  date: 'Monday February 5, 2018',
  thumbnail: images[0],
  title: 'History',
  description: "Show commit logs",
  content: 'Now let\'s browse them to see what we changed.\n      Fortunately for us, there\'s git log. Think of Git\'s log as a journal that remembers all the changes we\'ve committed so far, in the order we committed them.\n        <pre><code>git log</code></pre>\n        <p class="text-right"><a href="https://git-scm.com/docs/git-log">source</a></p>'
