  thumbnail: images[0],
  title: 'Undo',
  description: "Switch branches or restore working tree files",
  content: 'Files can be changed back to how they were at the last commit by using the command: git checkout -- <target>. Go ahead and get rid of all the changes since the last commit for octocat.txt\n        <pre><code>git checkout -- octocat.txt</code></pre>\n        <p class="text-right"><a href="https://git-scm.com/docs/git-checkout">source</a></p>'
