  title: 'Branching Out',
  description: "List, create, or delete branches",
  content: 'When developers are working on a feature or bug they\'ll often create a copy (aka. branch) of their code they can make separate commits to. Then when they\'re done they can merge this branch back into their main master branch.\n        <pre><code>git branch clean_up</code></pre>\n        <p class="text-right"><a href="https://git-scm.com/docs/git-branch">source</a></p>'
