<template lang="html">
<!-- Blog Subpage -->
<section class="blog pt-page pt-page-6" data-id="blog">
  <div class="progress-wrap" v-bind:class="[progress_class]"><div class="meter"><div class="percent" v-bind:style="{ width: progress_percent + '%' }"></div></div></div>
  <div class="border-block-top-110"></div>
  <div class="section-inner">
    <div class="section-title-block">
      <div class="section-title-wrapper clearfix">
        <h2 class="section-title">{{ name }}</h2>
        <h5 class="section-description">{{ description }}</h5>
      </div>
    </div>

    <!-- Services block -->
    <div class="block-title">
      <h3 v-on:click="loadPage">Git</h3>
    </div>

    <div class="row">
      <router-link v-for="blog in filtered" v-bind:key="blog.id" tag="div" class="col-sm-6 col-md-3 subpage-block" :to="{ name: 'blogdetail', params: { id: blog.id } }">
        <div class="service-block">
          <div class="service-info">
              <img :src="blog.thumbnail" :alt="blog.title">
              <h4>{{blog.title}}</h4>
              <p>{{blog.description}}</p>
          </div>
        </div>
      </router-link>
    </div>
    <!-- End of Services block -->

    <pagination :pagination="pagination"></pagination>
  </div>
</section>
<!-- End of Blog Subpage -->
</template>

<script>
import { mapState, mapActions } from 'vuex'
import Pagination from './../components/Pagination.vue'

export default {
    components: {
        Pagination
    },
    data () {
        return {
            name: 'Blog',
            description: 'Code, Technology, Information',
            // blogs:[],
            pagination:{
                current:1,
                pages:1,
                per_page:12,
                // dots:'&hellip;'
            },
            start:0,
            limit:12,
            progress_class:'',
            progress_percent:0,
        }
    },
    computed: {
        ...mapState({
            blogs: state => state.blogs.blogs
        }),
        filtered: function(){
            return this.blogs.slice(this.start, this.limit)
        }
    },
    async created(){
        var that = this;
        that.progress_class = 'show';
        setTimeout(function(){
        that.progress_class = 'show showed';
        }, 50);
        setTimeout(function(){
          that.progress_class = 'show showed loading';
        }, 200);
        this.$store.dispatch('blogs/getAllBlogs')
        console.log( this.$root );
        if(this.$route.params.paged){
            this.pagination.current = Number(this.$route.params.paged)
            this.start = (this.pagination.current - 1) * this.pagination.per_page
            this.limit = this.pagination.current * this.pagination.per_page
        }

        // test
        var timer = setInterval(function() {
          if(that.progress_percent >= 100){
            clearInterval(timer)
            // Load done and clear
            that.progress_percent = 0
            that.progress_class = '';
          }
          that.progress_percent += 1
        }, 5);
    },
    mounted(){
    },
    watch: {
        '$route'() {
            if(this.$route.params.paged){
                this.pagination.current = Number(this.$route.params.paged)
                this.start = (this.pagination.current - 1) * this.pagination.per_page
                this.limit = this.pagination.current * this.pagination.per_page
            }
        },
        blogs(val){
            if(this.blogs.length){
                this.pagination.pages = Math.ceil(this.blogs.length / this.pagination.per_page)
            }
        }
    },
    methods:{
      loadPage(paged){
        var that = this;
        that.progress_class = 'show';
        setTimeout(function(){
        that.progress_class = 'show showed';
        }, 50);
        setTimeout(function(){
          that.progress_class = 'show showed loading';
        }, 200);

        // test
        var timer = setInterval(function() {
          if(that.progress_percent >= 100){
            clearInterval(timer)
            // Load done and clear
            that.progress_percent = 0
            that.progress_class = '';
          }
          that.progress_percent += 1
        }, 5);
      }
    }
}
</script>

<style lang="scss" scoped>
ul.pagination {
    display: block;
    list-style: none;
    margin: 25px 0 5px;
    padding: 0;
    text-align: left;
    li {
        display: inline-block;
        margin: 0;
        padding: 0;
        a {
            display: inline-block;
            margin: 0 1px;
            width: 34px;
            height: 34px;
            color: #555;
            background-color: transparent;
            border: 1px solid #e5e5e5;
            text-align: center;
            &:hover {
                color: #fff;
                background-color: #ff724c;
                border-color: transparent;
            }
            i {
                font-size: 16px;
                line-height: 32px;
            }
        }
        &:last-child a {
            margin-right: 0;
        }
    }
}
</style>



